@media screen  and (min-width: 700px){
    .hobie-content{
        max-width: 500px;
    }    
}

.hobie-content{
    font-family: 'M PLUS Rounded 1c';
    font-family: 'Noto Sans KR';
    

}
.hobie-content-item{
    margin-top: 20px;
}
.hobie-content-item:nth-child(1){
    margin-top: 0px;}

.hobie-content span{
    margin-bottom: 5px;
    font-family: 'M PLUS Rounded 1c';
    font-family: 'Noto Sans KR';
    color: black;
    font-weight: bold;
    width:fit-content;
}
.hobie-content p{
margin-top: 5px;
line-height: 18px;
}